import { useCallback, useEffect, useRef } from "react";
import cx from "classnames";

import { useDataTableContext } from "../useDataTableContext";
import { TableHeaderCell } from "../../../base/cells/TableHeaderCell";
import { HeaderContext } from "@tanstack/react-table";
import { DataTableHeaderCellProps as BaseDataTableHeaderCellProps } from "../../../base/cells/TableHeaderCell";
import { SrOnly } from "../../../../../../components";

import styles from "./DataTableCells.module.scss";

/**
 * Props for the DataTableHeaderCell component
 * @extends BaseDataTableHeaderCellProps
 */
export type DataTableHeaderCellProps<T> = BaseDataTableHeaderCellProps & {
  cellPosition: { rowIndex: number; columnIndex: number };
  header: HeaderContext<T, unknown>;
};

/**
 * Data table header cell with focus management
 * @param props - The props for the DataTableHeaderCell component
 * @param props.cellPosition - The cell position of the cell
 * @param props.children - The children of the DataTableHeaderCell component
 * @param props.header - The header of the DataTableHeaderCell component
 */
export function DataTableHeaderCell<T>(props: DataTableHeaderCellProps<T>) {
  const { cellPosition, children, header, className, ...rest } = props;

  const headerCellRef = useRef<HTMLDivElement>(null);

  const tableContext = useDataTableContext();

  useEffect(() => {
    if (headerCellRef.current) {
      const sortingHandler = header.column.columnDef.enableSorting
        ? header.column.getToggleSortingHandler()
        : undefined;

      tableContext.registerCell({
        position: cellPosition,
        element: headerCellRef.current,
        isHeader: true,
        sortingHandler,
      });
      return () => {
        tableContext.unregisterCell(cellPosition);
      };
    }
  }, [cellPosition, tableContext, header.column]);

  const handleFocus = useCallback(() => {
    // handle focusing from outside the table or clicked
    if (!tableContext.isTableFocused) {
      tableContext.initializeFocus();
      tableContext.focusCell(cellPosition);
    }
  }, [cellPosition, tableContext]);

  const handleClick = useCallback(
    (event: React.MouseEvent) => {
      if (header.column.columnDef.enableSorting) {
        // Check if the click target is within a resize handle
        const target = event.target as HTMLElement;
        const resizeHandle = target.closest(
          '[aria-roledescription="Resize handle"]',
        );

        // If clicking on resize handle, don't trigger sorting
        if (resizeHandle) {
          return;
        }

        // Otherwise, trigger sorting
        header.column.getToggleSortingHandler()?.(event);
      }
    },
    [header.column],
  );

  const tabIndex = tableContext.getCellTabIndex(cellPosition);

  return (
    <TableHeaderCell
      type="data-table"
      {...rest}
      ref={headerCellRef}
      tabIndex={tabIndex}
      onFocus={handleFocus}
      onClick={handleClick}
      className={cx(styles["data-table-header-cell"], className)}
      aria-sort={
        header.column.getIsSorted() === "asc"
          ? "ascending"
          : header.column.getIsSorted() === "desc"
            ? "descending"
            : undefined
      }
    >
      {children}
      {header.column.columnDef.enableSorting && (
        <SrOnly>
          {header.column.getIsSorted() === "asc"
            ? "Sorted ascending."
            : header.column.getIsSorted() === "desc"
              ? "Sorted descending."
              : "Not sorted."}{" "}
          Click to toggle sort.
        </SrOnly>
      )}
    </TableHeaderCell>
  );
}
