import { forwardRef } from "react";

import { useDataTableContext } from "./useDataTableContext";

import { TableContainer, TableContainerProps } from "../../base/TableContainer";

/**
 * Data table container with focus management
 * @extends TableContainerProps
 */
export const DataTableContainer = forwardRef<
  HTMLDivElement,
  TableContainerProps
>(({ children, onFocus, onBlur, onKeyDown, tabIndex, ...rest }, ref) => {
  const tableContext = useDataTableContext();

  const handleFocus = (event: React.FocusEvent<HTMLDivElement>) => {
    onFocus?.(event);
    // Only initialize focus if the container itself received focus from outside
    // Not when focus moves between cells within the table
    if (event.target === event.currentTarget) {
      tableContext.initializeFocus();
    }
  };

  const handleBlur = (event: React.FocusEvent<HTMLDivElement>) => {
    onBlur?.(event);
    // Only blur if focus is leaving the table container entirely
    if (!event.currentTarget.contains(event.relatedTarget as Node)) {
      tableContext.handleTableBlur();
    }
  };

  const handleKeyDown = (event: React.KeyboardEvent<HTMLDivElement>) => {
    onKeyDown?.(event);
    tableContext.handleKeyDown(event);
  };

  return (
    <TableContainer
      ref={ref}
      {...rest}
      tabIndex={tabIndex ?? 0}
      onFocus={handleFocus}
      onBlur={handleBlur}
      onKeyDown={handleKeyDown}
    >
      {children}
    </TableContainer>
  );
});

DataTableContainer.displayName = "DataTableContainer";
