import { useCallback, useEffect, useRef } from "react";
import cx from "classnames";

import { useDataTableContext } from "../useDataTableContext";
import { TableFooterCell } from "../../../base/cells/TableFooterCell";
import { DataTableFooterCellProps as BaseDataTableFooterCellProps } from "../../../base/cells/TableFooterCell";

import styles from "./DataTableCells.module.scss";

/**
 * Props for the DataTableFooterCell component
 * @extends BaseDataTableFooterCellProps
 */
export type DataTableFooterCellProps = BaseDataTableFooterCellProps & {
  cellPosition: { rowIndex: number; columnIndex: number };
};

/**
 * Data table footer cell with focus management
 * @param props - The props for the DataTableFooterCell component
 * @param props.cellPosition - The cell position of the cell
 * @param props.children - The children of the DataTableFooterCell component
 */
export function DataTableFooterCell(props: DataTableFooterCellProps) {
  const { cellPosition, children, className, ...rest } = props;

  const footerCellRef = useRef<HTMLDivElement>(null);

  const tableContext = useDataTableContext();

  useEffect(() => {
    if (footerCellRef.current) {
      tableContext.registerCell({
        position: cellPosition,
        element: footerCellRef.current,
        isHeader: false,
      });
      return () => {
        tableContext.unregisterCell(cellPosition);
      };
    }
  }, [cellPosition, tableContext]);

  const handleFocus = useCallback(() => {
    // handle focusing from outside the table or clicked
    if (!tableContext.isTableFocused) {
      tableContext.initializeFocus();
      tableContext.focusCell(cellPosition);
    }
  }, [cellPosition, tableContext]);

  const tabIndex = tableContext.getCellTabIndex(cellPosition);

  return (
    <TableFooterCell
      type="data-table"
      {...rest}
      ref={footerCellRef}
      tabIndex={tabIndex}
      onFocus={handleFocus}
      className={cx(styles["data-table-footer-cell"], className)}
    >
      {children}
    </TableFooterCell>
  );
}
