import { useCallback, useRef, useState } from "react";
import type { Table as TanstackTable } from "@tanstack/react-table";
import { CellPosition, FocusableCell } from "./types";
import { useColumnOrder } from "./useColumnOrder";
import { useHandleKeyDown } from "./useHandleKeyDown";
import { useScrollIntoView } from "./useScrollIntoView";

export type RegisterCellProps = {
  position: CellPosition;
  element: HTMLElement;
  isHeader?: boolean;
  sortingHandler?: (event: React.KeyboardEvent | React.MouseEvent) => void;
};

/**
 * Hook to accessibly manage the focus state of a table
 * @param table - The table to manage the focus of
 * @param isExpandable - Whether the table supports row expansion
 */
export const useFocusManagement = <T>(
  table: TanstackTable<T>,
  isExpandable: boolean = false,
) => {
  const [focusedCell, setFocusedCell] = useState<CellPosition | null>(null);
  const [isTableFocused, setIsTableFocused] = useState(false);
  const [isOffGridInteractionOccurring, setIsOffGridInteractionOccurring] =
    useState(false);
  const cellRefs = useRef<Map<string, FocusableCell>>(new Map());
  const tableRef = useRef<HTMLDivElement>(null);

  const getCellKey = useCallback((position: CellPosition) => {
    return `${position.rowIndex}-${position.columnIndex}`;
  }, []);

  const registerCell = useCallback(
    ({
      position,
      element,
      isHeader = false,
      sortingHandler,
    }: RegisterCellProps) => {
      const key = getCellKey(position);
      cellRefs.current.set(key, {
        position,
        element,
        isHeader,
        sortingHandler,
      });
    },
    [getCellKey],
  );

  const unregisterCell = useCallback(
    (position: CellPosition) => {
      const key = getCellKey(position);
      cellRefs.current.delete(key);
    },
    [getCellKey],
  );

  const getTotalColumns = useCallback(() => {
    return table.getFlatHeaders().length;
  }, [table]);

  // Use column analysis hook for both visual order and accessible columns
  const { columnOrder } = useColumnOrder({ table });

  // Find the next valid cell position by checking if cells are actually registered
  const getNextValidPosition = useCallback(
    (
      currentPosition: CellPosition,
      direction: "up" | "down" | "left" | "right",
    ): CellPosition | null => {
      const totalColumns = getTotalColumns();

      let newRowIndex = currentPosition.rowIndex;
      let newColumnIndex = currentPosition.columnIndex;

      switch (direction) {
        case "up":
          newRowIndex = currentPosition.rowIndex - 1;
          break;
        case "down":
          newRowIndex = currentPosition.rowIndex + 1;
          break;
        case "left":
        case "right": {
          // Use visual column order for horizontal navigation
          const currentVisualIndex = columnOrder.indexOf(
            currentPosition.columnIndex,
          );

          if (currentVisualIndex === -1) {
            // Fallback to logical order if visual order fails
            if (direction === "left") {
              newColumnIndex = Math.max(0, currentPosition.columnIndex - 1);
            } else {
              newColumnIndex = Math.min(
                totalColumns - 1,
                currentPosition.columnIndex + 1,
              );
            }
          } else {
            // Navigate using visual order
            let targetVisualIndex;
            if (direction === "left") {
              targetVisualIndex = Math.max(0, currentVisualIndex - 1);
            } else {
              targetVisualIndex = Math.min(
                columnOrder.length - 1,
                currentVisualIndex + 1,
              );
            }
            newColumnIndex = columnOrder[targetVisualIndex];
          }
          break;
        }
      }

      if (direction === "up" || direction === "down") {
        if (newRowIndex < 0) {
          return null; // Can't go above first row
        }

        const targetKey = getCellKey({
          rowIndex: newRowIndex,
          columnIndex: newColumnIndex,
        });
        const targetCell = cellRefs.current.get(targetKey);

        if (!targetCell) {
          return null; // No cell registered at target position
        }
      }

      // Check if the position actually changed
      if (
        newRowIndex === currentPosition.rowIndex &&
        newColumnIndex === currentPosition.columnIndex
      ) {
        return null;
      }

      return { rowIndex: newRowIndex, columnIndex: newColumnIndex };
    },
    [getTotalColumns, getCellKey, columnOrder],
  );

  // Helper to detect if cell contains only a single focusable button
  const getSingleFocusableInCell = useCallback(
    (cellElement: HTMLElement): HTMLElement | null => {
      const focusableElements = cellElement.querySelectorAll(
        'button, input[type="checkbox"], input[type="radio"]',
      );

      if (focusableElements.length === 1) {
        return focusableElements[0] as HTMLElement;
      }

      return null;
    },
    [],
  );

  const updateAllTabIndices = useCallback(
    (newFocusedPosition: CellPosition) => {
      cellRefs.current.forEach((cell, key) => {
        const [rowIndex, columnIndex] = key.split("-").map(Number);
        const isFocusedCell =
          rowIndex === newFocusedPosition.rowIndex &&
          columnIndex === newFocusedPosition.columnIndex;

        const singleFocusable = getSingleFocusableInCell(cell.element);

        if (singleFocusable) {
          // For single-button cells, keep cell focusable and button out of tab sequence
          cell.element.tabIndex = isFocusedCell ? 0 : -1;
          singleFocusable.tabIndex = -1; // Always -1 to prevent Tab from focusing it
        } else {
          // For regular cells, manage cell tabIndex normally
          cell.element.tabIndex = isFocusedCell ? 0 : -1;
        }
      });
    },
    [getSingleFocusableInCell],
  );

  // Use scroll into view hook
  const [cellRef, setCellRef] = useState<HTMLElement | undefined>(undefined);
  useScrollIntoView({ table, element: cellRef });

  const updateCellFocusAndTabIndices = useCallback(
    (cell: FocusableCell, position: CellPosition) => {
      setFocusedCell(position);

      requestAnimationFrame(() => {
        updateAllTabIndices(position);

        // Check if cell contains only a single focusable
        const singleFocusable = getSingleFocusableInCell(cell.element);
        if (singleFocusable) {
          singleFocusable.focus();
        } else {
          cell.element.focus();
        }
        setCellRef(cell.element);
      });
    },
    [updateAllTabIndices, getSingleFocusableInCell],
  );

  const focusCell = useCallback(
    (position: CellPosition) => {
      const key = getCellKey(position);
      const cell = cellRefs.current.get(key);

      if (cell) {
        updateCellFocusAndTabIndices(cell, position);
      } else {
        // If cell isn't registered yet, try to find it in the next frame
        requestAnimationFrame(() => {
          const retryCell = cellRefs.current.get(key);
          if (retryCell) {
            updateCellFocusAndTabIndices(retryCell, position);
          }
        });
      }
    },
    [getCellKey, updateCellFocusAndTabIndices],
  );

  // Use handle key down hook
  const { handleKeyDown } = useHandleKeyDown({
    isTableFocused,
    focusedCell,
    getCellKey,
    cellRefs,
    getSingleFocusableInCell,
    setIsTableFocused,
    setFocusedCell,
    columnOrder,
    isOffGridInteractionOccurring,
    focusCell,
    getNextValidPosition,
    table,
    isExpandable,
  });

  // Initialize focus to first header cell when table gains focus
  const initializeFocus = useCallback(() => {
    setIsTableFocused(true);
    const firstHeaderPosition = { rowIndex: 0, columnIndex: 0 };

    // Manual focus for initialization
    const key = getCellKey(firstHeaderPosition);
    const cell = cellRefs.current.get(key);

    if (cell) {
      updateCellFocusAndTabIndices(cell, firstHeaderPosition);
    }
  }, [getCellKey, updateCellFocusAndTabIndices]);

  const handleTableBlur = useCallback(() => {
    setIsTableFocused(false);
    setFocusedCell(null);
  }, []);

  // Get tabIndex for a specific cell (for initial setup only, manual management takes over after)
  const getCellTabIndex = useCallback(
    (position: CellPosition): number => {
      // Check if we're managing this manually (cell is registered)
      const key = getCellKey(position);
      const cell = cellRefs.current.get(key);
      if (cell) {
        // Return the current DOM value since we're managing it manually
        return cell.element.tabIndex;
      }

      // Fallback for initial render before manual management kicks in
      if (!isTableFocused) {
        // If table is not focused, only the first header cell should be focusable
        return position.rowIndex === 0 && position.columnIndex === 0 ? 0 : -1;
      }

      if (!focusedCell) {
        // If no cell is focused yet, make first header cell focusable
        return position.rowIndex === 0 && position.columnIndex === 0 ? 0 : -1;
      }

      // Only the currently focused cell should have tabIndex 0
      return focusedCell.rowIndex === position.rowIndex &&
        focusedCell.columnIndex === position.columnIndex
        ? 0
        : -1;
    },
    [isTableFocused, focusedCell, getCellKey],
  );

  return {
    focusedCell,
    isTableFocused,
    tableRef,
    registerCell,
    unregisterCell,
    handleKeyDown,
    initializeFocus,
    handleTableBlur,
    getCellTabIndex,
    focusCell,
    isOffGridInteractionOccurring,
    setIsOffGridInteractionOccurring,
  };
};
