import { createContext } from "react";
import type { CellPosition } from "../../internal/focus-management/types";
import { RegisterCellProps } from "../../internal/focus-management/useFocusManagement";

/**
 * The context type for the table
 */
export interface DataTableContextType {
  hoveredColumnId: string | null;
  setHoveredColumnId: (columnId: string | null) => void;
  // Editable cell management
  isOffGridInteractionOccurring: boolean;
  setIsOffGridInteractionOccurring: (
    isOffGridInteractionOccurring: boolean,
  ) => void;
  // Focus management
  focusedCell: CellPosition | null;
  isTableFocused: boolean;
  registerCell: (props: RegisterCellProps) => void;
  unregisterCell: (position: CellPosition) => void;
  getCellTabIndex: (position: CellPosition) => number;
  handleKeyDown: (event: React.KeyboardEvent) => void;
  initializeFocus: () => void;
  handleTableBlur: () => void;
  focusCell: (position: CellPosition) => void;
  hasSubRows: boolean;
}

/**
 * The context for the table context provider
 */
export const DataTableContext = createContext<DataTableContextType | undefined>(
  undefined,
);
