import { ReactNode } from "react";
import {
  CellContext,
  createColumnHelper,
  ColumnDef as TanstackColumnDef,
} from "@tanstack/react-table";

import Flex from "../../../../components/Flex";

import { ColumnDef } from "../types";
import { TableEmptyCellContent } from "../base/cells/TableEmptyCellContent";

// Extend TanStack Table's ColumnMeta type to include our custom properties
declare module "@tanstack/react-table" {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  interface ColumnMeta<TData, TValue> {
    editMode?: ColumnDef<TData>["editMode"];
    onChange?: ColumnDef<TData>["onChange"];
    options?: ColumnDef<TData>["options"];
  }
}

/**
 * Props for the getTanStackColumnDef function
 * @extends ColumnDef<T>
 */
export type TanStackColumnDefProps<T> = {
  columnDef: ColumnDef<T>;
};

/**
 * Build the column definition for TanStack useReactTable
 * This allows for customized column definitions that maintain type safety
 * @param props - The props for the getTanStackColumnDef function
 * @param props.columnDef - The column definition for the TanStack useReactTable
 * @returns The column definition for the TanStack useReactTable
 */
export function getTanStackColumnDef<T>({
  columnDef,
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
}: TanStackColumnDefProps<T>): TanstackColumnDef<T, any> {
  const columnHelper = createColumnHelper<T>();

  // If this column has sub-columns, create a group column
  if (columnDef.columns && columnDef.columns.length > 0) {
    return columnHelper.group({
      id: columnDef.id as string,
      header: columnDef.headerLabel,
      columns: columnDef.columns.map((column) =>
        getTanStackColumnDef<T>({
          columnDef: column,
        }),
      ),
      footer: columnDef.footerContent
        ? () => columnDef.footerContent
        : undefined,
      enableResizing: columnDef.resizable,
    });
  }

  // Otherwise, create an accessor column
  return columnHelper.accessor((row: T) => row[columnDef.id], {
    id: columnDef.id as string,
    cell: (info: CellContext<T, unknown>) => {
      if (columnDef.renderCell) {
        const cellContent = columnDef.renderCell(
          info.getValue() as T[keyof T],
          info.row.depth,
        );
        return (
          <Flex
            justifyContent={columnDef.align}
            wrap="wrap"
            style={{
              width: "100%",
            }}
          >
            {cellContent ?? <TableEmptyCellContent />}
          </Flex>
        );
      }
      return (
        <Flex
          justifyContent={columnDef.align}
          wrap="wrap"
          style={{
            width: "100%",
          }}
        >
          {(info.getValue() as ReactNode) ?? <TableEmptyCellContent />}
        </Flex>
      );
    },
    header: () => (
      <Flex justifyContent={columnDef.align} wrap="wrap" grow={1}>
        {columnDef.headerLabel}
      </Flex>
    ),
    footer: columnDef.footerContent ? () => columnDef.footerContent : undefined,
    minSize: columnDef.minWidth,
    maxSize: columnDef.maxWidth,
    enableResizing: columnDef.resizable,
    enableSorting: !!columnDef.sortable,
    meta: {
      editMode: columnDef.editMode,
      onChange: columnDef.onChange,
      options: columnDef.options,
    },

    // If custom sorting function is provided, use it
    ...(columnDef.sortable && typeof columnDef.sortable === "function"
      ? {
          sortingFn: (rowA, rowB, colId) => {
            if (typeof columnDef.sortable === "function") {
              return columnDef.sortable(
                rowA.getValue(colId),
                rowB.getValue(colId),
              );
            }
            return 0;
          },
        }
      : undefined),
  });
}
