import { ComponentPropsWithoutRef, Fragment, ReactNode } from "react";
import cx from "classnames";
import { Table as TanstackTable, flexRender } from "@tanstack/react-table";

import { Flex } from "../../../../../components";

import { getCommonPinningStyles } from "../../internal/getCommonPinningStyles";
import { getCommonPinningClasses } from "../../internal/getCommonPinningClasses";
import { TableRow } from "../../types";
import { TableBody } from "../../base/TableBody";
import { DataTableBodyCell } from "./cells/DataTableBodyCell";

import styles from "./DataTableBody.module.scss";
import { useDataTableContext } from "./useDataTableContext";

/**
 * Props for the DataTableBody component
 * @extends {ComponentPropsWithoutRef<"div">}
 */
export type DataTableBodyProps<T> = {
  /**
   * The table instance from @tanstack/react-table to render
   */
  table: TanstackTable<T>;
} & ComponentPropsWithoutRef<"div">;

export const DataTableBodyContent = <T,>({
  table,
}: {
  table: TanstackTable<T>;
}) => {
  const { hasSubRows } = useDataTableContext();
  return table.getRowModel().rows.map((row, sortedIndex) => (
    <Fragment key={row.id}>
      <div
        role="row"
        aria-rowindex={sortedIndex + table.getHeaderGroups().length + 1} // aria-rowindex is 1-based, uses sorted position
        aria-expanded={
          hasSubRows && row.getCanExpand() ? row.getIsExpanded() : undefined
        }
        aria-level={
          hasSubRows &&
          (row.getCanExpand() || row.getParentRow()?.getCanExpand())
            ? row.depth + 1
            : undefined
        }
        className={styles["data-table-body-row"]}
      >
        {row.getVisibleCells().map((cell) => {
          const isLastInParentGroup = cell.column.parent
            ? cell.column.parent.getLeafColumns().slice(-1)[0] === cell.column
            : false;

          const cellClasses = cx(getCommonPinningClasses(cell.column), {
            [styles["data-table-body-cell-group"]]:
              "columns" in cell.column.columnDef ||
              (cell.column.parent && isLastInParentGroup),
          });
          return (
            <DataTableBodyCell<T>
              key={cell.id}
              aria-colindex={cell.column.getIndex() + 1} // aria-colindex is 1-based
              className={cellClasses}
              style={{
                minWidth: `calc(var(--a2-table-col-${cell.column.getIndex()}-size) * 1px)`,
                ...getCommonPinningStyles(cell.column),
              }}
              columnId={cell.column.id}
              cellPosition={{
                rowIndex: sortedIndex + table.getHeaderGroups().length,
                columnIndex: cell.column.getIndex(),
              }}
              editMode={cell.column.columnDef.meta?.editMode}
              editValue={cell.getContext().getValue() as T[keyof T]}
              onChange={cell.column.columnDef.meta?.onChange}
              options={cell.column.columnDef.meta?.options}
              rowId={row.id}
              isExpandCell={cell.column.id === "a2-table-internal-expand"}
              isSelectCell={cell.column.id === "a2-table-internal-select"}
            >
              {flexRender(cell.column.columnDef.cell, cell.getContext())}
            </DataTableBodyCell>
          );
        })}
      </div>
      {row.getIsExpanded() &&
        "subComponent" in (row.original as TableRow<T>) && (
          <div
            id={`${row.id}-sub-component`}
            aria-hidden={!row.getIsExpanded()}
            className={styles["data-table-body-row"]}
          >
            <div className={styles["data-table-body-sub-component-container"]}>
              <Flex className={styles["data-table-body-sub-component-content"]}>
                {(row.original as TableRow<T>).subComponent as ReactNode}
              </Flex>
            </div>
          </div>
        )}
    </Fragment>
  ));
};

export function DataTableBody<T>(props: DataTableBodyProps<T>) {
  const { table, ...rest } = props;
  return (
    <TableBody type="data-table" {...rest}>
      <DataTableBodyContent table={table} />
    </TableBody>
  );
}
