import {
  useRef,
  KeyboardEvent,
  useMemo,
  useState,
  useCallback,
  ChangeEvent,
} from "react";
import cx from "classnames";

import {
  Flex,
  ListView,
  Popover,
  SearchField,
  SrOnly,
} from "../../../../../../components";

import { DataTableEditableCellProps } from "./types";
import { TableBodyCell } from "../../../base/cells/TableBodyCell";
import { useEditCell } from "./useEditCell";

import styles from "./DataTableEditableCells.module.scss";
import dataTableCellStyles from "../cells/DataTableCells.module.scss";

/**
 * Props for the DataTableEditableMultiselectCell component
 * @extends DataTableEditableCellProps<string[]>
 */
export type DataTableEditableMultiselectCellProps = DataTableEditableCellProps<
  string[]
> & {
  /**
   * The options available for selection
   */
  options: { value: string; label: string }[];
};

/**
 * Editable multiselect cell for data tables
 * @param props - The props for the DataTableEditableMultiselectCell component
 * @param props.cellProps - The props for the DataTableEditableMultiselectCell component
 * @param props.children - The children of the DataTableEditableMultiselectCell component
 * @param props.rowId - The row id of the DataTableEditableMultiselectCell component
 * @param props.onChange - Called when the cell value is saved (on blur or enter/F2 key pressed)
 * @param props.cellPosition - The cell position of the DataTableEditableMultiselectCell component
 * @param props.value - The value of the DataTableEditableMultiselectCell component
 * @param props.options - The options available for selection
 */
export const DataTableEditableMultiselectCell = ({
  cellProps,
  children,
  rowId,
  onChange,
  cellPosition,
  value,
  options,
}: DataTableEditableMultiselectCellProps) => {
  const {
    isEditing,
    onClick,
    onKeyDown,
    saveValue,
    cancelEdit,
    editValue,
    updateEditValue,
  } = useEditCell<string[]>({
    initialValue: value,
    onChange,
    cellPosition,
    rowId,
  });
  const searchFieldRef = useRef<HTMLInputElement>(null);
  const [searchValue, setSearchValue] = useState("");
  const listViewRef = useRef<HTMLUListElement>(null);

  const { className, ...restCellProps } = cellProps;

  const cellClasses = cx(styles["data-table-body-select-cell"], className, {
    [dataTableCellStyles["data-table-body-cell-editing"]]: isEditing,
  });

  const selectedValues = options.filter((item) =>
    editValue.includes(item.value),
  );

  const handleTriggerKeyDown = (
    e: KeyboardEvent<HTMLButtonElement>,
    triggerFn: (e: KeyboardEvent<HTMLButtonElement>) => void,
  ) => {
    onKeyDown?.(e);
    triggerFn?.(e);
  };

  const handleListViewKeyDown = useCallback(
    (e: KeyboardEvent<HTMLDivElement>) => {
      if ((e.key === "Tab" && !e.shiftKey) || e.key === "F2") {
        e.preventDefault();
        e.stopPropagation();
        saveValue();
      }
      if (e.key === "Escape") {
        e.preventDefault();
        e.stopPropagation();
        cancelEdit();
        setSearchValue("");
      }
      if (e.key === "ArrowDown" || e.key === "ArrowUp") {
        e.preventDefault(); // prevent scrolling the page
      }
    },
    [saveValue, cancelEdit],
  );

  const handleSearchFieldKeyDown = useCallback(
    (e: KeyboardEvent<HTMLInputElement>) => {
      if (e.key === "Escape") {
        e.preventDefault();
        e.stopPropagation();
        cancelEdit();
        setSearchValue("");
      }
      if (e.key === "ArrowDown") {
        e.preventDefault();
        e.stopPropagation();
        listViewRef.current?.focus();
      }
    },
    [cancelEdit],
  );

  const handleListViewSelectionChange = useCallback(
    (value: unknown[]) => {
      if (isEditing) {
        updateEditValue(
          (
            value as unknown as {
              label: string;
              value: string;
              disabled: boolean;
            }[]
          ).map((item) => item.value),
        );
      }
    },
    [updateEditValue, isEditing],
  );

  const filteredItems = useMemo(() => {
    if (searchValue === "") return options;
    return options.filter((item) =>
      item.label.toLowerCase().includes(searchValue.toLowerCase()),
    );
  }, [options, searchValue]);

  const handleOnClickOutside = useCallback(() => {
    if (isEditing) {
      saveValue();
      setSearchValue("");
    }
  }, [saveValue, isEditing]);

  const handleOpenAnimationComplete = useCallback(() => {
    if (isEditing) {
      searchFieldRef.current?.focus();
    }
  }, [isEditing]);

  const handleSearchFieldChange = useCallback(
    (e: ChangeEvent<HTMLInputElement>) => {
      setSearchValue(e.target.value);
    },
    [],
  );

  return (
    <>
      <Popover
        open={isEditing}
        onOpenAnimationComplete={handleOpenAnimationComplete}
        onClickOutside={handleOnClickOutside}
        placement="bottom-start"
        disableCaret
      >
        <Popover.Trigger>
          {({ ref: popoverRef, onKeyDown: triggerOnKeyDown, ...rest }) => {
            return (
              <TableBodyCell
                {...restCellProps}
                className={cellClasses}
                onClick={onClick}
              >
                <button
                  {...rest}
                  ref={popoverRef}
                  className={styles["data-table-body-cell-select-trigger"]}
                  onKeyDown={(e) =>
                    handleTriggerKeyDown(e, () => triggerOnKeyDown?.(e))
                  }
                >
                  {children}
                </button>
              </TableBodyCell>
            );
          }}
        </Popover.Trigger>
        <Popover.Content>
          <Flex gap="4" direction="column">
            <SearchField
              ref={searchFieldRef}
              onChange={handleSearchFieldChange}
              onKeyDown={handleSearchFieldKeyDown}
            />
            <ListView
              defaultSelected={selectedValues}
              onKeyDown={handleListViewKeyDown}
              items={filteredItems}
              onSelectionChange={handleListViewSelectionChange}
              ref={listViewRef}
            >
              {({ items }) =>
                items.map((item) => (
                  <ListView.Option item={item} key={item.label}>
                    {item.label}
                  </ListView.Option>
                ))
              }
            </ListView>
          </Flex>
        </Popover.Content>
      </Popover>
      <SrOnly>
        {isEditing
          ? "Press F2 to save cell value. Press Escape to cancel."
          : "Press Enter or F2 to edit cell value."}
      </SrOnly>
    </>
  );
};
