import { ComponentPropsWithoutRef } from "react";
import { Table as TanstackTable } from "@tanstack/react-table";
import cx from "classnames";

import { getCommonPinningStyles } from "../../internal/getCommonPinningStyles";
import { getCommonPinningClasses } from "../../internal/getCommonPinningClasses";
import { getColSpanWidth } from "../../internal/getColSpanWidth";

import { ColumnDef } from "../../types";
import { DataTableProps } from "../DataTable";
import { TableFooter } from "../../base/TableFooter";
import { DataTableFooterCell } from "./cells/DataTableFooterCell";

import styles from "./DataTableFooter.module.scss";

/**
 * Props for the default/configured table footer rows
 * @extends TanstackTable<T>
 * @extends ColumnDef<T>[]
 */
export type DefaultTableFooterRowsProps<T> = {
  table: TanstackTable<T>;
  columns: ColumnDef<T>[];
};

/**
 * Props for the custom table footer rows
 * @extends DataTableProps<T>
 */
export type CustomTableFooterRowsProps<T> = {
  columnCount: number;
  customFooter: DataTableProps<T>["customFooter"];
  table: TanstackTable<T>;
};

/**
 * Props for the DataTableFooter component
 * @extends ComponentPropsWithoutRef<"div">
 */
export type DataTableFooterProps<T> = {
  /**
   * The table instance from @tanstack/react-table to render
   */
  table: TanstackTable<T>;
  /**
   * The columns of the table
   */
  columns: ColumnDef<T>[];
  /**
   * The custom footer of the table
   */
  customFooter?: DataTableProps<T>["customFooter"];
} & ComponentPropsWithoutRef<"div">;

function DefaultTableFooterRows<T>({
  table,
  columns,
}: DefaultTableFooterRowsProps<T>) {
  // Recursively find a column by ID in the column tree
  const findColumnById = (
    cols: ColumnDef<T>[],
    id: string,
  ): ColumnDef<T> | undefined => {
    for (const col of cols) {
      if (col.id === id) {
        return col;
      }
      if (col.columns && Array.isArray(col.columns)) {
        const found = findColumnById(col.columns, id);
        if (found) {
          return found;
        }
      }
    }
    return undefined;
  };

  // Recursively get the maximum number of footer rows from a column and its nested columns
  const getMaxFooterRows = (column: ColumnDef<T>): number => {
    let currentMax = 0;

    // Check current column's footer content
    if (Array.isArray(column.footerContent)) {
      currentMax = column.footerContent.length;
    } else if (column.footerContent) {
      currentMax = 1;
    }

    // Recursively check nested columns
    if (column.columns && Array.isArray(column.columns)) {
      const nestedMax = Math.max(
        ...column.columns.map((nestedCol) => getMaxFooterRows(nestedCol)),
        0,
      );
      currentMax = Math.max(currentMax, nestedMax);
    }

    return currentMax;
  };

  const maxFooterRows = Math.max(
    ...columns.map((column) => getMaxFooterRows(column)),
    1,
  );

  return Array.from({ length: maxFooterRows }, (_, rowIndex) => (
    <div
      key={`footer-row-${rowIndex}`}
      role="row"
      data-anv="table-footer-row"
      className={styles["data-table-footer-row"]}
      aria-rowindex={
        rowIndex +
        table.getHeaderGroups().length +
        table.getRowModel().rows.length +
        1
      }
    >
      {table
        .getFlatHeaders()
        .filter((header) => {
          const column = findColumnById(columns, header.column.id);
          // Filter out columns that have nested columns (column groups)
          return !column?.columns || column.columns.length === 0;
        })
        .map((header) => {
          const column = findColumnById(columns, header.column.id);

          let footerContent = null;
          if (Array.isArray(column?.footerContent)) {
            footerContent = column.footerContent[rowIndex] || null;
          } else if (column?.footerContent && rowIndex === 0) {
            footerContent = column.footerContent;
          }

          return (
            <DataTableFooterCell
              key={header.id}
              className={getCommonPinningClasses(header.column)}
              style={{
                minWidth: `calc(var(--a2-table-col-${header.column.getIndex()}-size) * 1px)`,
                ...getCommonPinningStyles(header.column),
              }}
              cellPosition={{
                rowIndex:
                  rowIndex +
                  table.getHeaderGroups().length +
                  table.getRowModel().rows.length,
                columnIndex: header.column.getIndex(),
              }}
            >
              {footerContent}
            </DataTableFooterCell>
          );
        })}
    </div>
  ));
}

function CustomTableFooterRows<T>({
  customFooter,
  columnCount,
  table,
}: CustomTableFooterRowsProps<T>) {
  return customFooter?.map((footerRow, rowIndex) => {
    return (
      <div
        key={rowIndex}
        role="row"
        data-anv="table-footer-row"
        className={styles["data-table-footer-row"]}
        aria-rowindex={
          rowIndex +
          table.getHeaderGroups().length +
          table.getRowModel().rows.length +
          1
        }
      >
        {(() => {
          let currentColumnIndex = 0;
          return Array.from({
            length:
              // total columns
              columnCount -
              // minus columns merged using colSpan
              footerRow.reduce((acc, curr) => acc + curr.colSpan, 0) +
              // plus columns added in the custom footer row
              footerRow.length,
          }).map((_, index) => {
            const footer = footerRow[index] ?? { content: "", colSpan: 1 };
            const minWidth = getColSpanWidth(
              footer.colSpan,
              currentColumnIndex,
            );

            const cell = (
              <DataTableFooterCell
                key={index}
                colSpan={footer.colSpan}
                style={{
                  minWidth,
                }}
                cellPosition={{
                  rowIndex:
                    rowIndex +
                    table.getHeaderGroups().length +
                    table.getRowModel().rows.length,
                  columnIndex: currentColumnIndex,
                }}
              >
                {footer.content}
              </DataTableFooterCell>
            );

            // Advance the column index by the colSpan for the next iteration
            currentColumnIndex += footer.colSpan;

            return cell;
          });
        })()}
      </div>
    );
  });
}

/**
 * Data table footer, which can be either a configured or custom footer
 * @param props - The props for the DataTableFooter component
 * @param props.table - The table instance from @tanstack/react-table to render
 * @param props.customFooter - The custom footer of the table
 * @param props.columns - The columns of the table
 */
export function DataTableFooter<T>({
  table,
  customFooter,
  columns,
  ...rest
}: DataTableFooterProps<T>) {
  const columnCount = table.getFlatHeaders().length;
  const classes = cx(styles["data-table-footer"], rest.className);

  return (
    <TableFooter type="data-table" {...rest} className={classes}>
      {customFooter ? (
        <CustomTableFooterRows
          table={table}
          customFooter={customFooter}
          columnCount={columnCount}
        />
      ) : (
        <DefaultTableFooterRows table={table} columns={columns} />
      )}
    </TableFooter>
  );
}
