import { MutableRefObject, useCallback } from "react";
import type { Table as TanstackTable } from "@tanstack/react-table";

import type { CellPosition, FocusableCell } from "./types";

export type UseHandleKeyDownProps<T> = {
  isTableFocused: boolean;
  focusedCell: CellPosition | null;
  getCellKey: (position: CellPosition) => string;
  cellRefs: MutableRefObject<Map<string, FocusableCell>>;
  getSingleFocusableInCell: (cellElement: HTMLElement) => HTMLElement | null;
  setIsTableFocused: (focused: boolean) => void;
  setFocusedCell: (position: CellPosition | null) => void;
  columnOrder: number[];
  focusCell: (position: CellPosition) => void;
  getNextValidPosition: (
    currentPosition: CellPosition,
    direction: "up" | "down" | "left" | "right",
  ) => CellPosition | null;
  table: TanstackTable<T>;
  isExpandable: boolean;
  isOffGridInteractionOccurring: boolean;
};

/**
 * Hook to handle keyboard events for the table
 * @param isTableFocused - Whether the table is focused
 * @param focusedCell - The currently focused cell
 * @param getCellKey - A function to get the key for a cell
 * @param cellRefs - A reference to the cells in the table
 * @param getSingleFocusableInCell - A function to get the single focusable in a cell
 * @param setIsTableFocused - A function to set the table focused state
 * @param setFocusedCell - A function to set the focused cell
 * @param getAccessibleColumnOrder - A function to get the accessible column order
 * @param focusCell - A function to focus a cell
 * @param getNextValidPosition - A function to get the next valid position
 * @param table - The TanStack table instance
 * @param isExpandable - Whether the table supports row expansion
 */
export const useHandleKeyDown = <T>({
  isTableFocused,
  focusedCell,
  getCellKey,
  cellRefs,
  getSingleFocusableInCell,
  setIsTableFocused,
  setFocusedCell,
  columnOrder,
  focusCell,
  getNextValidPosition,
  table,
  isExpandable,
  isOffGridInteractionOccurring,
}: UseHandleKeyDownProps<T>) => {
  const handleKeyDown = useCallback(
    (event: React.KeyboardEvent) => {
      if (!isTableFocused || !focusedCell || isOffGridInteractionOccurring)
        return;

      // Check if we're currently focused on a single focusable within a cell
      const currentCellKey = getCellKey(focusedCell);
      const currentCell = cellRefs.current.get(currentCellKey);
      const isOnSingleFocusable =
        currentCell &&
        getSingleFocusableInCell(currentCell.element) &&
        document.activeElement &&
        currentCell.element.contains(document.activeElement) &&
        document.activeElement !== currentCell.element;

      // Handle Tab when focused on single focusable - exit table
      if (event.key === "Tab" && isOnSingleFocusable) {
        // Let the tab event bubble up to exit the table
        setIsTableFocused(false);
        setFocusedCell(null);
        return;
      }

      // Handle expand/collapse with left/right arrows when table is expandable (treegrid)
      if (
        isExpandable &&
        (event.key === "ArrowLeft" || event.key === "ArrowRight")
      ) {
        const headerRowCount = table.getHeaderGroups().length;
        const bodyRowIndex = focusedCell.rowIndex - headerRowCount;

        // Only handle if we're in a body row (not header)
        if (bodyRowIndex >= 0) {
          const rows = table.getRowModel().rows;
          const currentRow = rows[bodyRowIndex];

          if (currentRow) {
            const canExpand = currentRow.getCanExpand();
            const isExpanded = currentRow.getIsExpanded();

            // Handle expand/collapse logic
            if (event.key === "ArrowRight" && canExpand && !isExpanded) {
              // Expand the row
              event.preventDefault();
              currentRow.getToggleExpandedHandler()();

              // Maintain focus on the same cell after expansion
              setTimeout(() => {
                focusCell(focusedCell);
              });
              return;
            } else if (event.key === "ArrowLeft" && canExpand && isExpanded) {
              // Collapse the row
              event.preventDefault();
              currentRow.getToggleExpandedHandler()();

              // Maintain focus on the same cell after collapse
              setTimeout(() => {
                focusCell(focusedCell);
              });
              return;
            }
          }
        }
      }

      let direction: "up" | "down" | "left" | "right" | null = null;

      switch (event.key) {
        case "ArrowUp":
          direction = "up";
          break;
        case "ArrowDown":
          direction = "down";
          break;
        case "ArrowLeft":
          direction = "left";
          break;
        case "ArrowRight":
          direction = "right";
          break;
        case "Home": {
          // Go to first accessible column (prioritizing visible pinned columns)
          focusCell({ ...focusedCell, columnIndex: columnOrder[0] });
          event.preventDefault();
          return;
        }
        case "End": {
          // Go to last accessible column (prioritizing visible pinned columns)
          focusCell({
            ...focusedCell,
            columnIndex: columnOrder[columnOrder.length - 1],
          });
          event.preventDefault();
          return;
        }
        case "Enter":
        case " ": {
          const currentCellKey = getCellKey(focusedCell);
          const currentCell = cellRefs.current.get(currentCellKey);

          if (currentCell) {
            // Check if this is a sortable header cell
            if (currentCell.isHeader && currentCell.sortingHandler) {
              event.preventDefault();
              currentCell.sortingHandler(event);
              return;
            }

            const singleFocusable = getSingleFocusableInCell(
              currentCell.element,
            );
            if (singleFocusable) {
              event.preventDefault();

              // Store current focus state before clicking
              const currentPosition = { ...focusedCell };

              // Click the focusable
              singleFocusable.click();

              // Restore focus after the click
              setTimeout(() => {
                const cellAfterClick = cellRefs.current.get(currentCellKey);
                if (cellAfterClick) {
                  const focusableAfterClick = getSingleFocusableInCell(
                    cellAfterClick.element,
                  );
                  if (focusableAfterClick) {
                    focusableAfterClick.focus();
                  } else {
                    // Fallback to cell if focusable is no longer single
                    cellAfterClick.element.focus();
                  }
                  // Ensure our state is still correct
                  if (
                    focusedCell?.rowIndex !== currentPosition.rowIndex ||
                    focusedCell?.columnIndex !== currentPosition.columnIndex
                  ) {
                    setFocusedCell(currentPosition);
                  }
                }
              });

              return;
            }
          }
          return;
        }
        default:
          return;
      }

      if (direction) {
        event.preventDefault();
        const nextPosition = getNextValidPosition(focusedCell, direction);
        if (nextPosition) {
          focusCell(nextPosition);
        }
      }
    },
    [
      isTableFocused,
      focusedCell,
      getCellKey,
      cellRefs,
      getSingleFocusableInCell,
      setIsTableFocused,
      setFocusedCell,
      columnOrder,
      focusCell,
      getNextValidPosition,
      table,
      isExpandable,
      isOffGridInteractionOccurring,
    ],
  );

  return { handleKeyDown };
};
