import { useCallback, useEffect, useRef } from "react";
import cx from "classnames";

import { SrOnly } from "../../../../../../components";

import { DataTableEditableCellEditIcon } from "./DataTableEditableCellEditIcon";
import { DataTableEditableCellProps } from "./types";
import { TableBodyCell } from "../../../base/cells/TableBodyCell";
import { useEditCell } from "./useEditCell";

import styles from "./DataTableEditableCells.module.scss";
import dataTableCellStyles from "../cells/DataTableCells.module.scss";

/**
 * Props for the DataTableEditableTextCell component
 * @extends DataTableEditableCellProps<string>
 */
export type DataTableEditableTextCellProps = DataTableEditableCellProps<string>;
/**
 * Editable text cell for data tables
 * @param props - The props for the DataTableEditableTextCell component
 * @param props.cellProps - The props for the DataTableEditableTextCell component
 * @param props.children - The children of the DataTableEditableTextCell component
 * @param props.rowId - The row id of the DataTableEditableTextCell component
 * @param props.onChange - Called when the cell value is saved (on blur or enter/F2 key pressed)
 * @param props.cellPosition - The cell position of the DataTableEditableTextCell component
 * @param props.value - The value of the DataTableEditableTextCell component
 */
export const DataTableEditableTextCell = ({
  cellProps,
  children,
  rowId,
  onChange,
  cellPosition,
  value,
}: DataTableEditableTextCellProps) => {
  const { isEditing, editValue, updateEditValue, onClick, onKeyDown, onBlur } =
    useEditCell<string>({
      initialValue: value,
      onChange,
      cellPosition,
      rowId,
    });

  const inputRef = useRef<HTMLInputElement>(null);

  const { className, ...restCellProps } = cellProps;

  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      updateEditValue(e.target.value);
    },
    [updateEditValue],
  );

  useEffect(() => {
    if (isEditing) {
      inputRef.current?.focus();
    }
  }, [isEditing]);

  const classes = cx(className, styles["data-table-body-cell-editable"], {
    [dataTableCellStyles["data-table-body-cell-editing"]]: isEditing,
  });

  return (
    <TableBodyCell
      {...restCellProps}
      className={classes}
      onClick={onClick}
      onKeyDown={onKeyDown}
    >
      {isEditing ? (
        <>
          <input
            type="text"
            className={styles["data-table-body-cell-input"]}
            ref={inputRef}
            value={editValue}
            onChange={handleChange}
            onBlur={onBlur}
          />
        </>
      ) : (
        <>
          {children}
          {!isEditing && <DataTableEditableCellEditIcon />}
        </>
      )}
      <SrOnly>
        {isEditing
          ? "Press F2 to save cell value."
          : "Press Enter or F2 to edit cell value. Press Escape to cancel."}
      </SrOnly>
    </TableBodyCell>
  );
};
