import { KeyboardEvent } from "react";
import cx from "classnames";

import { Menu } from "../../../../../../components/Menu";

import { SrOnly } from "../../../../../../components/SrOnly";

import { DataTableEditableCellEditIcon } from "./DataTableEditableCellEditIcon";
import { DataTableEditableCellProps } from "./types";
import { TableBodyCell } from "../../../base/cells/TableBodyCell";
import { useEditCell } from "./useEditCell";

import styles from "./DataTableEditableCells.module.scss";
import dataTableCellStyles from "../cells/DataTableCells.module.scss";

/**
 * Props for the DataTableEditableSelectCell component
 * @extends DataTableEditableCellProps<string>
 */
export type DataTableEditableSelectCellProps =
  DataTableEditableCellProps<string> & {
    /**
     * The options available for selection
     */
    options: { value: string; label: string }[];
  };

/**
 * Editable select cell for data tables
 * @param props - The props for the DataTableEditableSelectCell component
 * @param props.cellProps - The props for the DataTableEditableSelectCell component
 * @param props.children - The children of the DataTableEditableSelectCell component
 * @param props.rowId - The row id of the DataTableEditableSelectCell component
 * @param props.onChange - Called when the cell value is saved (on blur or enter/F2 key pressed)
 * @param props.cellPosition - The cell position of the DataTableEditableSelectCell component
 * @param props.value - The value of the DataTableEditableSelectCell component
 * @param props.options - The options available for selection
 */
export const DataTableEditableSelectCell = ({
  cellProps,
  children,
  rowId,
  onChange,
  cellPosition,
  value,
  options,
}: DataTableEditableSelectCellProps) => {
  const {
    isEditing,
    onClick,
    onKeyDown,
    saveValue,
    updateEditValue,
    cancelEdit,
  } = useEditCell<string>({
    initialValue: value,
    onChange,
    cellPosition,
    rowId,
  });

  const { className, ...restCellProps } = cellProps;

  const cellClasses = cx(
    styles["data-table-body-cell-editable"],
    styles["data-table-body-select-cell"],
    className,
    { [dataTableCellStyles["data-table-body-cell-editing"]]: isEditing },
  );

  const handleKeyDown = (
    e: KeyboardEvent<HTMLButtonElement>,
    triggerFn: (e: KeyboardEvent<HTMLButtonElement>) => void,
  ) => {
    if (e.key === "ArrowDown" && !isEditing) {
      onKeyDown?.(e);
      return; // prevents menu from blocking table navigation
    }

    onKeyDown?.(e);
    triggerFn?.(e);
  };

  return (
    <>
      <Menu
        // TODO: remove this
        // used now to prevent empty menu from showing
        open={isEditing}
        onOutsidePress={saveValue}
        trigger={({
          ref: triggerRef,
          onKeyDown: triggerOnKeyDown,
          ...rest
        }) => {
          return (
            <TableBodyCell
              {...restCellProps}
              className={cellClasses}
              onClick={onClick}
            >
              <button
                {...rest}
                ref={triggerRef}
                className={styles["data-table-body-cell-select-trigger"]}
                onKeyDown={(e) => handleKeyDown(e, () => triggerOnKeyDown?.(e))}
              >
                {children}
              </button>
              {!isEditing && <DataTableEditableCellEditIcon />}
            </TableBodyCell>
          );
        }}
      >
        {isEditing &&
          options.map((item) => {
            return (
              <Menu.Item
                key={item.value}
                label={item.label}
                onClick={() => {
                  updateEditValue(item.value);
                  saveValue();
                }}
                onKeyDown={(e) => {
                  if (e.key === "Escape" || e.key === "F2") {
                    cancelEdit();
                  }
                }}
              />
            );
          })}
      </Menu>
      <SrOnly>
        {isEditing
          ? "Press Enter or Space to select a cell value. Press Escape or F2 to cancel."
          : "Press Enter or F2 to edit cell value."}
      </SrOnly>
    </>
  );
};
