import { useCallback, useMemo, useRef, useState } from "react";
import { useDataTableContext } from "../useDataTableContext";
import { UseEditCellProps, UseEditCellReturn } from "./types";

/**
 * Hook for common logic for editable cells in data tables
 * @param props - The props for the useEditCell hook
 * @param props.initialValue - The initial value of the cell
 * @param props.onChange - Called when the cell value is saved (on blur or enter/F2 key pressed)
 * @param props.cellPosition - The cell position of the cell
 * @param props.rowId - The row id of the cell
 * @returns Object containing the editing state and functions for editing the cell
 */
export const useEditCell = <T>({
  initialValue,
  onChange,
  cellPosition,
  rowId,
}: UseEditCellProps<T>): UseEditCellReturn<T> => {
  const [isEditing, setIsEditing] = useState(false);
  const [editValue, setEditValue] = useState<T>(initialValue);
  const editValueRef = useRef<T>(initialValue);
  const {
    focusCell,
    isTableFocused,
    initializeFocus,
    setIsOffGridInteractionOccurring,
  } = useDataTableContext();

  const handleEditStateChange = useCallback(
    (isEditing: boolean) => {
      setIsEditing(isEditing);
      setIsOffGridInteractionOccurring(isEditing);
    },
    [setIsOffGridInteractionOccurring],
  );

  const updateEditValue = useCallback((value: T) => {
    editValueRef.current = value;
    setEditValue(value);
  }, []);

  const handleFocusCell = useCallback(() => {
    if (!isTableFocused) {
      // Table not focused yet - initialize and focus this cell
      initializeFocus();
      setTimeout(() => {
        focusCell(cellPosition);
      });
    } else {
      // Table already focused - just update to this cell position
      setTimeout(() => {
        focusCell(cellPosition);
      });
    }
  }, [cellPosition, focusCell, isTableFocused, initializeFocus]);

  const handleSave = useCallback(
    (value: T) => {
      onChange(value, rowId);
      handleEditStateChange(false);
      handleFocusCell();
    },
    [onChange, rowId, handleFocusCell, handleEditStateChange],
  );

  const saveValue: UseEditCellReturn<T>["saveValue"] = useCallback(() => {
    handleSave(editValueRef.current);
  }, [handleSave]);

  const cancelEdit: UseEditCellReturn<T>["cancelEdit"] = useCallback(() => {
    setEditValue(initialValue);
    handleEditStateChange(false);
    handleFocusCell();
  }, [initialValue, handleFocusCell, handleEditStateChange]);

  const handleClick = useCallback(() => {
    if (isEditing) return;

    handleEditStateChange(true);
  }, [isEditing, handleEditStateChange]);

  const handleKeyDown = useCallback(
    (event: React.KeyboardEvent<HTMLElement>) => {
      if (event.key === "Enter" || event.key === "F2") {
        if (isEditing) {
          saveValue();
        } else {
          handleEditStateChange(true);
        }
      }
      if (event.key === "Escape") {
        cancelEdit();
      }
    },
    [isEditing, saveValue, cancelEdit, handleEditStateChange],
  );

  const handleBlur = useCallback(() => {
    saveValue();
  }, [saveValue]);

  const returnValue = useMemo(
    () => ({
      isEditing,
      editValue,
      updateEditValue,
      saveValue,
      cancelEdit,
      onClick: handleClick,
      onKeyDown: handleKeyDown,
      onBlur: handleBlur,
    }),
    [
      isEditing,
      editValue,
      updateEditValue,
      saveValue,
      cancelEdit,
      handleClick,
      handleKeyDown,
      handleBlur,
    ],
  );

  return returnValue;
};
