import { ComponentPropsWithoutRef, useCallback, useMemo } from "react";
import {
  getCoreRowModel,
  getSortedRowModel,
  useReactTable,
  ColumnPinningState,
  RowSelectionState,
  TableOptions,
  ExpandedState,
  getExpandedRowModel,
  getGroupedRowModel,
  SortingState,
  HeaderContext,
  CellContext,
} from "@tanstack/react-table";
import cx from "classnames";

import { LayoutUtilProps } from "../../../../types";
import { useLayoutPropsUtil } from "../../../../internal/hooks/useLayoutPropsUtil";

import Checkbox from "../../../../components/Checkbox";
import Button from "../../../../components/Button";
import Flex from "../../../../components/Flex";
import ExpandIcon from "../../../../../public/assets/icons/material/round/chevron_right.svg";

import { ColumnDef, CustomTableFooterCellProps, TableRow } from "../types";
import { TableBase } from "../base/TableBase";
import { DataTableBody } from "./internal/DataTableBody";
import { TableContainerProps } from "../base/TableContainer";
import { DataTableFooter } from "./internal/DataTableFooter";
import { getTanStackColumnDef } from "../internal/getTanStackColumnDef";
import { DataTableContextProvider } from "./internal/DataTableContextProvider";
import { DataTableHeader } from "./internal/DataTableHeader";
import { DataTableContainer } from "./internal/DataTableContainer";
import { DataTablePagination } from "./internal/DataTablePagination";
import { useOptionallyControlledState } from "../../../../internal";
import { DataTablePaginationConfig } from "./types";

import styles from "./DataTable.module.scss";
import { Spinner, SrOnly } from "../../../../components";
import { useAsyncData } from "./internal/useAsyncData";

/**
 * Represents a sorted column configuration for DataTable
 */
export type SortedColumn = {
  /**
   * The column identifier
   */
  id: string;
  /**
   * Whether the column is sorted in descending order
   */
  desc: boolean;
};

/**
 * Props for the DataTable component
 * @extends LayoutUtilProps
 * @extends ComponentPropsWithoutRef<"div">
 */
export type DataTableProps<T> = LayoutUtilProps &
  ComponentPropsWithoutRef<"div"> & {
    /**
     * The columns for the table, created with the `createColumnHelper` factory function.
     */
    columns: ColumnDef<T>[];
    /**
     * The background color of the table container
     * @default "default"
     */
    background?: TableContainerProps["background"];
    /**
     * The custom footer content for the table. Will be overridden by the footer content of the columns.
     */
    customFooter?: CustomTableFooterCellProps[][];
    /**
     * The data for the table. Must be an array of objects with an `id` property.
     */
    data?: TableRow<T>[] | Promise<TableRow<T>[]>;
    /**
     * The default expanded row ids. Can be "all" to expand all rows, or an array of row ids to expand specific rows.
     */
    defaultExpandedRowIds?: "all" | TableRow<T>["id"][];
    /**
     * The default selected row ids.
     */
    defaultSelectedRowIds?: TableRow<T>["id"][];
    /**
     * The default sorted column.
     */
    defaultSortedColumn?: SortedColumn;
    /**
     * The expanded row ids.
     */
    expandedRowIds?: "all" | TableRow<T>["id"][];
    /**
     * Whether the table is selectable. Can be a boolean or a function to customize the selection logic.
     * @default false
     */
    isSelectable?: boolean | ((row: T) => boolean);
    /**
     * Called when the row expansion state changes. Passes the expanded row ids.
     */
    onExpandRow?: (expandedRowIds: "all" | TableRow<T>["id"][]) => void;
    /**
     * Called when the row selection state changes. Passes the selected row ids.
     */
    onSelectRow?: (selectedRowIds: TableRow<T>["id"][]) => void;
    /**
     * Called when the column sorting state changes. Passes the sorted column.
     */
    onSort?: (sortedColumn: SortedColumn | undefined) => void;
    /**
     * Pagination configuration. When provided, enables pagination and filters data to show only the current page.
     * When `true`, the default pagination configuration is used.
     * When `false` or `undefined`, no pagination is applied and all data is shown.
     */
    pagination?: boolean | DataTablePaginationConfig<T>;
    /**
     * The selected row ids.
     */
    selectedRowIds?: TableRow<T>["id"][];
    /**
     * The sorted column.
     */
    sortedColumn?: SortedColumn;
  };

/**
 * Data table component for complex data grid layouts
 *
 * Features:
 * - Built-in focus management for accessibility
 * - Column resizing
 * - Column pinning
 * - Column grouping
 * - Controlled or uncontrolled column sorting
 * - Row selection
 * - Row expansion
 * - Row sub-component
 * - Row sub-rows
 * - Default and Strong backgrounds
 * - Pagination support with configurable items per page
 *
 * @param props - The props for the DataTable component
 * @param props.data - The data for the table. Must be an array of objects with an `id` property.
 * @param props.columns - The columns for the table, created with the `createColumnHelper` factory function.
 * @param props.background - The background color of the table container
 * @param props.customFooter - The custom footer content for the table. Will be overridden by the footer content of the columns.
 * @param props.defaultExpandedRowIds - The default expanded row ids.
 * @param props.defaultSelectedRowIds - The default selected row ids.
 * @param props.defaultSortedColumn - The default sorted column.
 * @param props.expandedRowIds - The expanded row ids.
 * @param props.isSelectable - Whether the table is selectable. Can be a boolean or a function to customize the selection logic.
 * @param props.onSelectRow - Called when the row selection state changes. Passes the selected row ids.
 * @param props.onSort - Called when the column sorting state changes. Passes the sorted column.
 * @param props.onExpandRow - Called when the row expansion state changes. Passes the expanded row ids.
 * @param props.pagination - Pagination configuration. When provided, enables pagination and filters data to show only the current page. When undefined, all data is shown and no pagination controls are rendered.
 * @param props.selectedRowIds - The selected row ids.
 * @param props.sortedColumn - The sorted column.
 * @param props.style - The style of the table container.
 * @param props.rest - The rest of the props for the DataTable component, spread to the div surrounding the table.
 *
 * @example
 * <DataTable
 *   data={tableData}
 *   columns={columns}
 *   pagination={{
 *     itemsPerPage: 25,
 *     defaultPageIndex: 0,
 *     onPageChange: (pageIndex) => console.log('Page changed to:', pageIndex),
 *     showCount: true,
 *   }}
 * />
 */
export function DataTable<T>(props: DataTableProps<T>) {
  const { layoutStyles, componentProps } = useLayoutPropsUtil(props);
  const {
    data: dataProp,
    columns: columnsProp,
    background = "default",
    customFooter,
    defaultExpandedRowIds,
    defaultSelectedRowIds,
    defaultSortedColumn,
    expandedRowIds,
    isSelectable,
    onSelectRow,
    onSort,
    onExpandRow,
    pagination,
    selectedRowIds,
    sortedColumn,
    style,
    ...rest
  } = componentProps;
  const styleCombined = {
    ...style,
    ...layoutStyles,
  };

  const [rowSelection, setRowSelection] =
    useOptionallyControlledState<RowSelectionState>({
      controlledValue:
        isSelectable && selectedRowIds
          ? Object.fromEntries(selectedRowIds.map((id) => [id, true]))
          : undefined,
      defaultValue: isSelectable
        ? defaultSelectedRowIds
          ? Object.fromEntries(defaultSelectedRowIds.map((id) => [id, true]))
          : {}
        : {},
      onChange: (newRowSelection) => {
        onSelectRow?.(Object.keys(newRowSelection) as TableRow<T>["id"][]);
      },
    });

  const [sorting, setSorting] = useOptionallyControlledState<SortingState>({
    controlledValue: sortedColumn ? [sortedColumn] : undefined,
    defaultValue: defaultSortedColumn ? [defaultSortedColumn] : undefined,
    onChange: (newSorting) => {
      onSort?.(newSorting[0]);
    },
  });

  const isPaginated = !!pagination;
  const paginationObject =
    typeof pagination === "object" ? pagination : undefined;
  const rowsPerPage = paginationObject?.rowsPerPage ?? 25;

  const [currentPageIndex, setCurrentPageIndex] =
    useOptionallyControlledState<number>({
      controlledValue: paginationObject?.currentPageIndex,
      defaultValue: paginationObject?.defaultPageIndex ?? 0,
      onChange: (newPageIndex) => {
        paginationObject?.onPageChange?.(newPageIndex);
      },
    });

  const pageLoader = paginationObject?.loadPageData;
  const isAsyncPaginated = !!pageLoader && typeof pageLoader === "function";

  const dataSource = useMemo(() => {
    if (!isAsyncPaginated) {
      return dataProp;
    }

    return pageLoader({
      pageIndex: currentPageIndex ?? 0,
      pageSize: rowsPerPage,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps -- pageLoader omitted intentionally
  }, [isAsyncPaginated, currentPageIndex, rowsPerPage, dataProp]);

  const {
    data: resolvedData,
    //TODO: Table should represent error state
    error: _error,
    isLoading,
  } = useAsyncData(dataSource);
  const totalItemCount =
    paginationObject?.totalRowCount ?? resolvedData?.length ?? 0;

  const hasSubRows = !!resolvedData?.some(
    (row) => row?.subRows?.length ?? 0 > 0,
  );
  const hasSubComponent = !!resolvedData?.some(
    (row) => row?.subComponent != null,
  );
  const isExpandable = hasSubRows || hasSubComponent;

  const [expanded, setExpanded] = useOptionallyControlledState<ExpandedState>({
    controlledValue:
      isExpandable && expandedRowIds
        ? expandedRowIds === "all"
          ? true
          : Object.fromEntries(expandedRowIds.map((id) => [id, true]))
        : undefined,
    defaultValue: isExpandable
      ? defaultExpandedRowIds
        ? defaultExpandedRowIds === "all"
          ? true
          : Object.fromEntries(defaultExpandedRowIds.map((id) => [id, true]))
        : {}
      : {},
    onChange: (newExpanded) => {
      if (newExpanded === true) {
        onExpandRow?.("all");
      } else {
        onExpandRow?.(Object.keys(newExpanded) as TableRow<T>["id"][]);
      }
    },
  });

  const tableData = useMemo(() => {
    // If the data is still loading, show no data
    if (isLoading) return [];
    // If pagination is not enabled or is async, return the data as is.
    if (!isPaginated || isAsyncPaginated) return resolvedData;

    const maxPageIndex = Math.max(
      0,
      Math.ceil(totalItemCount / rowsPerPage) - 1,
    );
    // Clamp page index to valid range
    const clampedPageIndex = Math.max(
      0,
      Math.min(currentPageIndex ?? 0, maxPageIndex),
    );
    const startIndex = clampedPageIndex * rowsPerPage;
    const endIndex = startIndex + rowsPerPage;

    return resolvedData?.slice(startIndex, endIndex) ?? [];
  }, [
    isLoading,
    isPaginated,
    isAsyncPaginated,
    resolvedData,
    totalItemCount,
    rowsPerPage,
    currentPageIndex,
  ]);

  // Calculate column pinning state (including select/expand columns if needed)
  const columnPinning = useMemo((): ColumnPinningState => {
    const { left, right } = {
      left: columnsProp
        .filter((column) => column.pinned === "left")
        .map((c) => String(c.id)),
      right: columnsProp
        .filter((column) => column.pinned === "right")
        .map((c) => String(c.id)),
    };

    // If any are pinned left, we want to also pin select/expand columns
    const isSelectableOrExpandable = isSelectable || isExpandable;
    const standardPinnedColumns = [];
    if (isSelectable) {
      standardPinnedColumns.push("a2-table-internal-select");
    }
    if (isExpandable) {
      standardPinnedColumns.push("a2-table-internal-expand");
    }

    return {
      left:
        isSelectableOrExpandable && left && left.length > 0
          ? [...standardPinnedColumns, ...left]
          : (left ?? []),
      right: right ?? [],
    };
  }, [columnsProp, isSelectable, isExpandable]);

  // Build columns for TanStack useReactTable
  const columns = useMemo(() => {
    const tableColumns: TableOptions<T>["columns"] = columnsProp.map((column) =>
      getTanStackColumnDef({
        columnDef: column,
      }),
    );

    // Add expand column if subRows are present
    if (isExpandable) {
      tableColumns.unshift({
        id: "a2-table-internal-expand",
        size: 44,
        header: ({ table }: HeaderContext<T, unknown>) => {
          return (
            <Flex grow={1} justifyContent="center">
              <Button
                onClick={table.getToggleAllRowsExpandedHandler()}
                appearance="ghost"
                className={cx(styles["expand-button"], {
                  [styles["expanded"]]: table.getIsAllRowsExpanded(),
                })}
                size="small"
                icon={ExpandIcon}
                aria-label={
                  table.getIsAllRowsExpanded()
                    ? "Collapse all rows"
                    : "Expand all rows"
                }
              />
            </Flex>
          );
        },
        cell: ({ row }: CellContext<T, unknown>) => {
          if (row.getCanExpand()) {
            return (
              <Button
                onClick={row.getToggleExpandedHandler()}
                appearance="ghost"
                size="small"
                icon={ExpandIcon}
                className={cx(styles["expand-button"], {
                  [styles["expanded"]]: row.getIsExpanded(),
                })}
                aria-label={row.getIsExpanded() ? "Collapse row" : "Expand row"}
                aria-expanded={
                  hasSubComponent && row.getCanExpand()
                    ? row.getIsExpanded()
                    : undefined
                }
                aria-controls={
                  hasSubComponent && row.getCanExpand()
                    ? `${row.id}-sub-component`
                    : undefined
                }
              />
            );
            // show a checkbox for nested rows if isSelectable is true
          } else if (row.getParentRow()?.getCanExpand() && isSelectable) {
            return (
              <Checkbox
                aria-label={row.getIsSelected() ? "Deselect row" : "Select row"}
                checked={row.getIsSelected()}
                disabled={!row.getCanSelect()}
                indeterminate={row.getIsSomeSelected()}
                onChange={row.getToggleSelectedHandler()}
              />
            );
          }
        },
      });
    }

    // Add select column if isSelectable is true
    if (isSelectable) {
      tableColumns.unshift({
        id: "a2-table-internal-select",
        size: 44,
        header: ({ table }: HeaderContext<T, unknown>) => (
          <Flex grow={1} justifyContent="center">
            <Checkbox
              aria-label={
                table.getIsAllRowsSelected()
                  ? "Deselect all rows"
                  : "Select all rows"
              }
              checked={table.getIsAllRowsSelected()}
              indeterminate={table.getIsSomeRowsSelected()}
              onChange={table.getToggleAllRowsSelectedHandler()}
            />
          </Flex>
        ),
        cell: ({ row }: CellContext<T, unknown>) => {
          if (row.getParentRow()?.getCanExpand() && isSelectable) {
            return null;
          }
          return (
            <Checkbox
              aria-label={row.getIsSelected() ? "Deselect row" : "Select row"}
              checked={row.getIsSelected()}
              disabled={!row.getCanSelect()}
              indeterminate={row.getIsSomeSelected()}
              onChange={row.getToggleSelectedHandler()}
            />
          );
        },
      });
    }

    return tableColumns;
  }, [columnsProp, hasSubComponent, isExpandable, isSelectable]);

  const table = useReactTable({
    data: tableData ?? [],
    columns,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getExpandedRowModel: getExpandedRowModel(),
    getGroupedRowModel: getGroupedRowModel(),
    getRowId: (row) => (row as TableRow<T>).id.toString(),
    getSubRows: (row) => (row as TableRow<T>).subRows ?? undefined,
    getRowCanExpand: (row) => {
      const originalRow = row.original as TableRow<T>;
      return (
        (originalRow?.subRows?.length ?? 0) > 0 ||
        originalRow?.subComponent != null
      );
    },
    state: {
      columnPinning,
      rowSelection,
      expanded,
      sorting,
    },
    columnResizeMode: "onChange",
    enableRowSelection:
      typeof isSelectable === "function"
        ? (row) => isSelectable(row.original)
        : isSelectable,
    onRowSelectionChange: (newRowSelection) => {
      setRowSelection(newRowSelection as RowSelectionState);
    },
    onExpandedChange: (newExpanded) => {
      setExpanded(newExpanded as ExpandedState);
    },
    onSortingChange: (updaterOrValue) => {
      const newSorting =
        typeof updaterOrValue === "function"
          ? updaterOrValue(sorting)
          : updaterOrValue;

      const sortingChanged =
        JSON.stringify(newSorting) !== JSON.stringify(sorting);
      if (!sortingChanged) {
        return;
      }

      setSorting(newSorting);
    },
  });

  const headers = table.getFlatHeaders();
  const headerRowCount = table.getHeaderGroups().length;
  const bodyRowCount = table.getExpandedRowModel().rows.length;
  const columnSizing = table.getState().columnSizing;

  const getMaxFooterRows = useCallback((column: ColumnDef<T>): number => {
    let currentMax = 0;

    // Check current column's footer content
    if (Array.isArray(column.footerContent)) {
      currentMax = column.footerContent.length;
    } else if (column.footerContent) {
      currentMax = 1;
    }

    // Recursively check nested columns and find the maximum
    if (column.columns && Array.isArray(column.columns)) {
      const nestedMax = Math.max(
        ...column.columns.map((nestedCol) => getMaxFooterRows(nestedCol)),
        0,
      );
      currentMax = Math.max(currentMax, nestedMax);
    }

    return currentMax;
  }, []);

  const footerLength = useMemo(() => {
    return Math.max(
      ...columnsProp.map((column) => getMaxFooterRows(column)),
      0,
    );
  }, [columnsProp, getMaxFooterRows]);

  const hasFooters =
    footerLength > 0 || (customFooter !== undefined && customFooter.length > 0);

  const rowCount = useMemo(
    () => bodyRowCount + headerRowCount + footerLength,
    [bodyRowCount, headerRowCount, footerLength],
  );

  /**
   * Create CSS variables for the column sizes and hovered column
   */
  const cssVars = useMemo(() => {
    const vars: { [key: string]: number | string } = {};
    for (let i = 0; i < headers.length; i++) {
      const header = headers[i]!;
      vars[`--a2-table-col-${header.column.getIndex()}-size`] =
        header.column.getSize();
    }
    return vars;
    // eslint-disable-next-line react-hooks/exhaustive-deps -- columnSizing is needed to trigger recalculation when columns resize
  }, [headers, columnSizing]);

  const columnCount = useMemo(() => {
    // don't count grouped column header cells
    return headers.filter((header) => header.subHeaders?.length === 0).length;
  }, [headers]);

  return (
    <DataTableContextProvider table={table} hasSubRows={hasSubRows}>
      <DataTableContainer
        {...rest}
        style={styleCombined}
        background={background}
        className={pagination ? styles["container-with-pagination"] : undefined}
      >
        <TableBase
          type="data-table"
          style={cssVars}
          rowCount={rowCount}
          colCount={columnCount}
          hasSubRows={hasSubRows}
        >
          <DataTableHeader table={table} />
          <DataTableBody table={table} />
          {isLoading && (
            <>
              <SrOnly>Loading data. Please wait.</SrOnly>
              {/* Visual spacer to avoid collapsing the table body area */}
              <div
                style={{
                  height: rowsPerPage ? `${rowsPerPage * 35}px` : "200px",
                }}
                role="presentation"
                aria-hidden="true"
              >
                <Flex
                  justifyContent="center"
                  alignItems="center"
                  style={{
                    position: "absolute",
                    top: 0,
                    bottom: 0,
                    left: 0,
                    right: 0,
                    margin: "auto",
                    zIndex: 3,
                  }}
                >
                  <Spinner />
                </Flex>
              </div>
            </>
          )}
          {hasFooters && (
            <DataTableFooter
              className={
                pagination ? styles["footer-with-pagination"] : undefined
              }
              table={table}
              columns={columnsProp}
              customFooter={customFooter}
            />
          )}
        </TableBase>
        {pagination && (
          <DataTablePagination
            itemsPerPage={rowsPerPage}
            showCount={paginationObject?.showCount ?? true}
            page={(currentPageIndex ?? 0) + 1}
            totalItemCount={totalItemCount}
            onPageChange={(page) => {
              setCurrentPageIndex(page - 1);
            }}
          />
        )}
      </DataTableContainer>
    </DataTableContextProvider>
  );
}
