import { useEffect, useMemo, useRef } from "react";
import type { Table as TanstackTable } from "@tanstack/react-table";

/**
 * Hook to scroll an table cell into view when using keyboard navigation
 * @param table - The table to scroll into view
 */
export const useScrollIntoView = <T>({
  table,
  element,
}: {
  table: TanstackTable<T>;
  element?: HTMLElement;
}) => {
  const isScrollingRef = useRef(false);
  const scrollTimeoutRef = useRef<number>();

  const { leftWidth, rightWidth } = useMemo(() => {
    return {
      leftWidth: table.getLeftTotalSize(),
      rightWidth: table.getRightTotalSize(),
    };
  }, [table]);

  // Scroll element into view to prevent sticky headers/footers from covering the cell
  useEffect(() => {
    if (!element) return;

    const isBodyCell = element.getAttribute("data-anv") === "table-body-cell";

    const scrollContainer = element.closest(
      '[data-anv="table-scroll-container"]',
    ) as HTMLElement;

    const headerElement =
      scrollContainer.querySelectorAll('[role="rowgroup"]')[0];
    const footerElement =
      scrollContainer.querySelectorAll('[role="rowgroup"]')[2];

    const stickyDimensions = {
      headerHeight: headerElement.getBoundingClientRect().height,
      footerHeight: footerElement
        ? footerElement.getBoundingClientRect().height
        : 0,
      leftWidth,
      rightWidth,
    };

    // Detect user scrolling to prevent auto-scroll during manual scroll
    const handleScroll = () => {
      isScrollingRef.current = true;

      if (scrollTimeoutRef.current) {
        clearTimeout(scrollTimeoutRef.current);
      }

      scrollTimeoutRef.current = window.setTimeout(() => {
        isScrollingRef.current = false;
      }, 150);
    };

    scrollContainer.addEventListener("scroll", handleScroll);

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (!entry.isIntersecting && !isScrollingRef.current) {
          const scrollContainerRect = scrollContainer.getBoundingClientRect();
          const getScrollByLeft = () => {
            // Element is under left pinned columns
            if (
              entry.boundingClientRect.x <
              scrollContainerRect.x + stickyDimensions.leftWidth
            ) {
              return (
                -stickyDimensions.leftWidth +
                entry.boundingClientRect.x -
                scrollContainerRect.x
              );
            }
            // Element is under right pinned columns
            if (
              entry.boundingClientRect.x + entry.boundingClientRect.width >
              scrollContainerRect.x +
                scrollContainerRect.width -
                stickyDimensions.rightWidth
            ) {
              return (
                element.offsetLeft +
                (!isBodyCell ? scrollContainerRect.x : 0) + // header/footer have different offsetParent
                entry.boundingClientRect.width -
                scrollContainerRect.x -
                scrollContainerRect.width -
                scrollContainer.scrollLeft +
                stickyDimensions.rightWidth
              );
            }
            return 0;
          };

          const getScrollByTop = () => {
            if (!isBodyCell) return 0;
            // Element is under header
            if (
              entry.boundingClientRect.y <
              scrollContainerRect.y + stickyDimensions.headerHeight
            ) {
              return (
                -stickyDimensions.headerHeight +
                entry.boundingClientRect.y -
                scrollContainerRect.y
              );
            }
            // Element is under footer
            if (
              entry.boundingClientRect.y + entry.boundingClientRect.height >
              scrollContainerRect.y +
                scrollContainerRect.height -
                stickyDimensions.footerHeight
            ) {
              return (
                element.offsetTop +
                entry.boundingClientRect.height -
                scrollContainerRect.y -
                scrollContainerRect.height -
                scrollContainer.scrollTop +
                stickyDimensions.footerHeight
              );
            }
            return 0;
          };

          scrollContainer.scrollBy({
            left: getScrollByLeft(),
            top: getScrollByTop(),
            behavior: "smooth",
          });
        }
      },
      {
        root: scrollContainer,
        rootMargin: `-${stickyDimensions.headerHeight}px -${stickyDimensions.rightWidth}px -${stickyDimensions.footerHeight}px -${stickyDimensions.leftWidth}px 
      `,
        threshold: 1.0,
      },
    );
    observer.observe(element);

    return () => {
      observer.disconnect();
      scrollContainer.removeEventListener("scroll", handleScroll);
      if (isScrollingRef.current) isScrollingRef.current = false;
      if (scrollTimeoutRef.current) {
        clearTimeout(scrollTimeoutRef.current);
        scrollTimeoutRef.current = undefined;
      }
    };
  }, [element, leftWidth, rightWidth]);
};
