import { ComponentPropsWithoutRef } from "react";
import { flexRender, Table as TanstackTable } from "@tanstack/react-table";

import IconArrowUpward from "../../../../../../public/assets/icons/material/round/arrow_upward.svg";
import IconArrowDownward from "../../../../../../public/assets/icons/material/round/arrow_downward.svg";

import { TableHeader } from "../../base/TableHeader";
import { useDataTableContext } from "./useDataTableContext";
import { DataTableHeaderCell } from "./cells/DataTableHeaderCell";
import { Flex, Icon } from "../../../../../components";
import { getColSpanWidth } from "../../internal/getColSpanWidth";
import { getCommonPinningClasses } from "../../internal/getCommonPinningClasses";
import { getCommonPinningStyles } from "../../internal/getCommonPinningStyles";
import { ResizeHandle } from "../../internal/ResizeHandle";

import styles from "./DataTableHeader.module.scss";

/**
 * Props for the DataTableHeader component
 * @extends TanstackTable<T>
 * @extends ComponentPropsWithoutRef<"div">
 */
type DataTableHeaderProps<T> = {
  table: TanstackTable<T>;
} & ComponentPropsWithoutRef<"div">;

/**
 * Data table header container
 * @param props - The props for the DataTableHeader component
 * @param props.table - The table instance from @tanstack/react-table to render
 */
export function DataTableHeader<T>({ table }: DataTableHeaderProps<T>) {
  const { setHoveredColumnId } = useDataTableContext();

  return (
    <TableHeader type="data-table">
      {table.getHeaderGroups().map((headerGroup, index) => (
        <div
          key={headerGroup.id}
          className={styles["data-table-header-row"]}
          role="row"
          aria-rowindex={index + 1}
        >
          {headerGroup.headers.map((header) => {
            const isLastInParentGroup = header.column.parent
              ? header.column.parent.getLeafColumns().slice(-1)[0] ===
                header.column
              : false;

            const isInternalSelectColumn =
              header.column.id === "a2-table-internal-select";
            const isInternalExpandColumn =
              header.column.id === "a2-table-internal-expand";

            const minWidth = getColSpanWidth(
              header.colSpan,
              header.column.columns?.length > 0
                ? header.column.columns[0].getIndex()
                : header.column.getIndex(),
            );

            return (
              <DataTableHeaderCell
                header={header.getContext()}
                key={header.id}
                className={getCommonPinningClasses(header.column)}
                style={{
                  minWidth,
                  ...getCommonPinningStyles(header.column),
                }}
                aria-colspan={header.colSpan}
                isGroup={
                  "columns" in header.column.columnDef ||
                  (header.getContext().header.column.parent &&
                    isLastInParentGroup)
                }
                isSortable={header.column.columnDef.enableSorting}
                isSelectCell={isInternalSelectColumn}
                isExpandCell={isInternalExpandColumn}
                onMouseEnter={
                  isInternalExpandColumn || isInternalSelectColumn
                    ? undefined
                    : () => setHoveredColumnId(header.column.id)
                }
                onMouseLeave={
                  isInternalExpandColumn || isInternalSelectColumn
                    ? undefined
                    : () => setHoveredColumnId(null)
                }
                cellPosition={{
                  rowIndex: index,
                  columnIndex: header.column.getIndex(),
                }}
              >
                {header.isPlaceholder ? null : (
                  <Flex
                    direction="row"
                    alignItems="flex-end"
                    gap={2}
                    style={{
                      width: "100%",
                    }}
                  >
                    <Flex direction="row" alignItems="center" gap={2} grow={1}>
                      {flexRender(
                        header.column.columnDef.header,
                        header.getContext(),
                      )}
                      {{
                        asc: <Icon svg={IconArrowUpward} size="small" />,
                        desc: <Icon svg={IconArrowDownward} size="small" />,
                      }[header.column.getIsSorted() as string] ?? null}
                    </Flex>
                    {header.column.columnDef.enableResizing && (
                      <ResizeHandle header={header} />
                    )}
                  </Flex>
                )}
              </DataTableHeaderCell>
            );
          })}
        </div>
      ))}
    </TableHeader>
  );
}
