import { ReactNode } from "react";
import { ColumnDef } from "./types";

/**
 * Utility type that allows a value to be either the original type or,
 * if it's an array, one of the elements in the array
 */
export type ValueOrElement<V> = V extends (infer U)[] ? V | U : V;

/**
 * Factory function to create column helper function for defining Table columns
 *
 * @param T - The type of the data
 * @returns A function that creates a column definition
 */
export function createColumnHelper<T>() {
  return function createColumn<K extends keyof T>(
    id: K | { group: string },
    column: Omit<
      ColumnDef<T>,
      "id" | "onChange" | "options" | "renderCell" | "sortable"
    > & {
      renderCell?: (value: T[K], rowDepth: number) => ReactNode;
      sortable?: boolean | ((valueA: T[K], valueB: T[K]) => number);
      onChange?: (value: T[K], rowId: string) => void;
      options?: { value: ValueOrElement<T[K]>; label: string }[];
    },
  ): ColumnDef<T> {
    if (typeof id === "object" && "group" in id) {
      return { id: id.group, ...column } as ColumnDef<T>;
    }
    return { id, ...column } as ColumnDef<T>;
  };
}
