import { memo, Ref, useCallback, useEffect, useRef } from "react";
import cx from "classnames";

import { useDataTableContext } from "../useDataTableContext";
import {
  TableBodyCell,
  TableBodyCellProps,
} from "../../../base/cells/TableBodyCell";
import { DataTableBodyCellProps as BaseDataTableBodyCellProps } from "../../../base/cells/TableBodyCell";
import { ColumnDef } from "../../../types";
import { DataTableEditableTextCell } from "../editable-cells/DataTableEditableTextCell";
import { DataTableEditableSelectCell } from "../editable-cells/DataTableEditableSelectCell";
import { DataTableEditableMultiselectCell } from "../editable-cells/DataTableEditableMultiselectCell";

import styles from "./DataTableCells.module.scss";

/**
 * Props for the DataTableBodyCell component
 * @extends Omit<BaseDataTableBodyCellProps, "onChange">
 */
export type DataTableBodyCellProps<T> = Omit<
  BaseDataTableBodyCellProps,
  "onChange"
> & {
  /**
   * The cell position of the cell
   */
  cellPosition: { rowIndex: number; columnIndex: number };
  /**
   * The column id of the cell
   */
  columnId: string;
  /**
   * The edit mode of the cell
   */
  editMode?: ColumnDef<T>["editMode"];
  /**
   * The edit value of the cell
   */
  editValue?: T[keyof T];
  /**
   * Called when the cell value is saved (on blur or enter/F2 key pressed)
   */
  onChange?: ColumnDef<T>["onChange"];
  /**
   * The options of the cell
   */
  options?: ColumnDef<T>["options"];
  /**
   * The row id of the cell
   */
  rowId: string;
};

/**
 * DataTableBodyCellElement component with focus management and editable cells
 * @param props - The props for the DataTableBodyCellElement component
 * @param props.cellPosition - The cell position of the cell
 * @param props.columnId - The column id of the cell
 * @param props.children - The children of the DataTableBodyCellElement component
 * @param props.tabIndex - The tab index of the cell
 * @param props.editMode - The edit mode of the cell
 * @param props.editValue - The edit value of the cell
 * @param props.onChange - Called when the cell value is saved (on blur or enter/F2 key pressed)
 * @param props.options - The options of the cell
 * @param props.rowId - The row id of the cell
 * @param props.rest - The rest of the props
 */
function DataTableBodyCellElement<T>(props: DataTableBodyCellProps<T>) {
  const {
    cellPosition,
    columnId,
    children,
    editMode,
    editValue,
    onChange,
    options,
    rowId,
    ...rest
  } = props;

  const elementRef = useRef<HTMLDivElement>(null);

  const tableContext = useDataTableContext();

  // Register/unregister cell for focus management (data tables only)
  useEffect(() => {
    if (elementRef.current) {
      tableContext.registerCell({
        position: cellPosition,
        element: elementRef.current,
        isHeader: false,
      });
      return () => {
        tableContext.unregisterCell(cellPosition);
      };
    }
  }, [cellPosition, tableContext]);

  const isEditable = !!editMode;

  // Handle direct cell focus (when user clicks on cell)
  const handleFocus = useCallback(() => {
    if (!tableContext.isTableFocused) {
      // Table not focused yet - initialize and focus this cell
      tableContext.initializeFocus();
      tableContext.focusCell(cellPosition);

      // For editable cells, let the editable cell components handle their own focus
      // after table is initialized
    } else if (!isEditable) {
      tableContext.focusCell(cellPosition);
    }
  }, [tableContext, cellPosition, isEditable]);

  const tabIndex = tableContext.getCellTabIndex(cellPosition);

  const classes = cx(styles["data-table-body-cell"], rest.className, {
    [styles["data-table-body-cell-hovered"]]:
      tableContext?.hoveredColumnId === columnId,
  });

  const commonCellProps: TableBodyCellProps & { ref: Ref<HTMLDivElement> } = {
    type: "data-table",
    ...rest,
    className: classes,
    ref: elementRef,
    tabIndex,
    onFocus: handleFocus,
  };

  if (editMode === "text") {
    return (
      <DataTableEditableTextCell
        cellProps={commonCellProps}
        cellPosition={cellPosition}
        rowId={rowId}
        onChange={onChange as (value: string, rowId: string) => void}
        value={editValue as string}
      >
        {children}
      </DataTableEditableTextCell>
    );
  }

  if (editMode === "select") {
    return (
      <DataTableEditableSelectCell
        cellProps={commonCellProps}
        cellPosition={cellPosition}
        rowId={rowId}
        value={editValue as string}
        onChange={onChange as (value: string, rowId: string) => void}
        options={options as { value: string; label: string }[]}
      >
        {children}
      </DataTableEditableSelectCell>
    );
  }

  if (editMode === "multiselect") {
    return (
      <DataTableEditableMultiselectCell
        cellProps={commonCellProps}
        cellPosition={cellPosition}
        rowId={rowId}
        value={editValue as string[]}
        onChange={onChange as (value: string[], rowId: string) => void}
        options={options as { value: string; label: string }[]}
      >
        {children}
      </DataTableEditableMultiselectCell>
    );
  }

  return <TableBodyCell {...commonCellProps}>{children}</TableBodyCell>;
}

export const DataTableBodyCell = memo(DataTableBodyCellElement) as <T>(
  props: DataTableBodyCellProps<T>,
) => JSX.Element;
