import cx from "classnames";
import { Header } from "@tanstack/react-table";

import styles from "./ResizeHandle.module.scss";

/**
 * Props for the ResizeHandle component
 * @extends Header<T, unknown>
 */
export type ResizeHandleProps<T> = {
  header: Header<T, unknown>;
};

/**
 * Resize handle component resizing table columns
 * @param props - The props for the ResizeHandle component
 * @param props.header - The header of the ResizeHandle component
 */
export function ResizeHandle<T>({ header }: ResizeHandleProps<T>) {
  const classes = cx(styles["resize-handle"], {
    [styles["resizing"]]: header.column.getIsResizing(),
  });

  return (
    <div
      role="button"
      // TODO: enable keyboard resizing
      tabIndex={-1}
      onDoubleClick={() => header.column.resetSize()}
      onMouseDown={header.getResizeHandler()}
      onTouchStart={header.getResizeHandler()}
      className={classes}
      aria-label="Resize column"
      aria-pressed={header.column.getIsResizing()}
      aria-roledescription="Resize handle"
    />
  );
}
