/**
 * Get the width of a column with a colspan greater than 1
 * @param colSpan - The number of columns to span
 * @param cellIndex - The index of the cell
 * @returns The width of the column span
 */
export const getColSpanWidth = (colSpan: number, cellIndex: number): string => {
  let minWidth = `calc(var(--a2-table-col-${cellIndex}-size) * 1px)`;
  if (colSpan > 1) {
    const varsToAdd = Array.from(
      { length: colSpan },
      (_, i) => `var(--a2-table-col-${cellIndex + i}-size)`,
    );
    minWidth = `calc((${varsToAdd.join(" + ")}) * 1px)`;
  }
  return minWidth;
};
