import { Pagination, PaginationProps } from "../../../../..";
import { useDataTableContext } from "./useDataTableContext";

import styles from "./DataTablePagination.module.scss";
import { useCallback } from "react";

export type DataTablePaginationProps = Pick<
  PaginationProps,
  "page" | "itemsPerPage" | "totalItemCount" | "showCount" | "onPageChange"
>;

export const DataTablePagination = ({
  showCount = true,
  ...props
}: DataTablePaginationProps) => {
  const { setIsOffGridInteractionOccurring } = useDataTableContext();

  const handleFocus = useCallback(() => {
    setIsOffGridInteractionOccurring(true);
  }, [setIsOffGridInteractionOccurring]);

  const handleBlur = useCallback(() => {
    setIsOffGridInteractionOccurring(false);
  }, [setIsOffGridInteractionOccurring]);

  return (
    <Pagination
      className={styles["pagination"]}
      {...props}
      showCount={showCount}
      onFocus={handleFocus}
      onBlur={handleBlur}
    />
  );
};
